<template>
  <div class="game-screen">
    <div class="options">
      <button class="expand-settings-button" @click="colorSettingsClick" :style="colorSettingsClosed ? {background: '#efefef'} : {color: 'white', background: 'rgb(2,0,36)',background: 'linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%)'}">
        Colors
      </button>
      <div id="color-settings" class="expand-settings" :style="colorSettingsClosed ? {height: 0, opacity: 0, pointerEvents: 'none'} : {height: '250px'}">
        <div class="check-row">
          <label>Background Color</label>
          <input v-model="backgroundColor" type="color" />
        </div>
        <div v-if="colorType=='choose'" class="check-row">
          <label>Color</label>
          <input v-model="color" type="color" />
        </div>
        <div v-else class="check-col">
          <label>Color transition speed</label>
          <input v-model="colorTransitionSpeed" type="number"/>
        </div>
        <form class="check-row">
          <input type="radio" id="choose-color" value="choose" v-model="colorType"/>
          <label for="choose-color"> Choose Color </label>
          <br>
          <input type="radio" id="random-colors" value="random" v-model="colorType"/>
          <label for="random-colors"> Random Colors </label>
          <br>
          <input type="radio" id="change-colors" value="change" v-model="colorType"/>
          <label for="change-colors"> Changing Colors </label>
          <br>
          <input type="radio" id="change-colors-gray" value="change-gray" v-model="colorType"/>
          <label for="change-colors-gray"> Changing Colors (Gray) </label>
        </form>
        <br>
        <div class="check-row">
          <label>Keep Color Trails?</label>
          <input type="checkbox" v-model="keepColorTrails"/>
        </div>
      </div>
      <button class="expand-settings-button" @click="physicsSettingsClick" :style="physicsSettingsClosed ? {background: '#efefef'} : {background: '#F46A35', color: 'white'}">
        Physics
      </button>
      <div id="physics-settings" class="expand-settings" :style="physicsSettingsClosed ? {height: 0, opacity: 0, pointerEvents: 'none'} : {height: '520px'}">
        <div class="check-row">
          <label>Collissions?</label>
          <input type="checkbox" v-model="collissionsActive"/>
        </div>
        <div class="check-row">
          <label>Side Walls?</label>
          <input type="checkbox" v-model="sideWalls"/>
        </div>
        <label>Gravity</label>
        <input v-model="gravity" type="number" step="0.1" min="0.0" />
        <label>Bounciness</label>
        <input v-model="bouncePercent" type="number" step="0.1" min="0.0"/>
        <label>Ball Radius</label>
        <input v-model="radius" type="number" step="5" min="5" max="200"/>
        <label>Wind (x)</label>
        <input v-model="wind" type="number" step=".1"/>
        <label>Bounce randomness Y (%)</label>
        <input v-model="bounceRandomness" type="number" min="0" step="10" />
        <label>Bounce randomness X (%)</label>
        <input v-model="bounceRandomnessX" type="number" min="0" step="10" />
        <label>Delay between drops (ms)</label>
        <input v-model="dropDelay" type="number" step="5"/>
        <label>Delay between frames (ms)</label>
        <input v-model="frameDelay" type="number" step="5"/>
        <div>
          <button @click="changeAllVx(-2)"> Decrease Velocity (-x)</button>
          <button @click="changeAllVx(2)"> Increase Velocity (x)</button>
        </div>
        <div>
          <button @click="changeAllVy(-2)"> Decrease Velocity (-y)</button>
          <button @click="changeAllVy(2)"> Increase Velocity (y)</button>
        </div>
      </div>
      <button id="main-settings-button" class="expand-settings-button" @click="mainSettingsClick" :style="mainSettingsClosed ? {background: '#efefef'} : {background: 'gray', color: 'white'}">
        Main
      </button>
      <div id="main-settings" class="expand-settings" :style="mainSettingsClosed ? {height: 0, opacity: 0, pointerEvents: 'none'} : {height: '380px'}">
        <form class="check-row">
          <input type="radio" id="choose-color" value="center" v-model="xDrop"/>
          <label for="choose-color"> Centered Drop </label>
          <br>
          <input type="radio" id="random-colors" value="pattern" v-model="xDrop"/>
          <label for="random-colors"> Pattern Drop </label>
          <br>
          <input type="radio" id="change-colors" value="random" v-model="xDrop"/>
          <label for="change-colors"> Random Drop </label>
          <br>
        </form>
        <br>
        <label>Average Speed: {{averageBallSpeed}}</label>
        <label>Number balls: {{this.objects.length}}</label>
        <button @click="addBall"> Add Ball</button>
        <button @click="addManyBalls(10)">Add 10</button>
        <button @click="addManyBalls(100)">Add 100</button>
        <br>
        <button @click="reset">Drop</button>
        <button @click="clear">Clear All</button>
        <button style="background: grey" @click="resetAll">Reset All</button>
      </div>
    </div>
    <canvas id="game-canvas" width="600" height="800" :style="'background: '+backgroundColor"/>
  </div>
</template>

<script>
//multiple of gravity and wind used to round low velocities to 0
const velocityRound = 4;
const randomColors = ['#4285F4', '#EA4335', '#FBBC05', '#34A853'];
export default {
  name: 'Game',
  data(){
    return{
      colorSettingsClosed: true,
      physicsSettingsClosed: true,
      mainSettingsClosed: true,

      backgroundColor: '#ffffff',
      color: '#0095DD',
      colorType: 'change',
      colorFrom: null,
      colorTo: null,
      colorCurrent: null,
      colorTransitionSpeed: 10,
      keepColorTrails: false,

      collissionsActive: false,
      sideWalls: true,
      gravity: .20,
      bouncePercent: 0.9,
      wind: 0.0,
      bounceRandomness: 5,
      bounceRandomnessX: 30,
      dropDelay: 20,  
      frameDelay: 0,
      radius: 15,
      xDrop: 'center',

      averageBallSpeed: 0,
      canvas: null,
      ctx: null,
      objects: [],
    }
  },
  created(){
    this.addManyBalls(100);
  },
  mounted(){
    this.canvas = document.getElementById("game-canvas");
    const vh = Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0);
    const vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0);

    if(vh-80 < this.canvas.height)
      this.canvas.height = vh - 80;
    this.canvas.style.height = this.canvas.height + 'px';
    if(vw-60 < this.canvas.width)
      this.canvas.width = vw - 60;
    this.canvas.style.width  =  this.canvas.width + 'px';
    if(document.getElementsByClassName('options')[0].style.height < this.canvas.height - 10)
      document.getElementsByClassName('options')[0].style.height = (this.canvas.height - 10) + 'px';

    setInterval(() => this.averageBallSpeed = this.getAverageBallSpeed(), 500);
    this.ctx = this.canvas.getContext("2d");
    this.drawFrame();
  },
  methods: {
    updateObjPosition(i){
      var obj = this.objects[i];
      obj.x += obj.dx;
      obj.y += obj.dy;
      if(!(obj.dx == 0 && ((obj.x == this.canvas.width-this.radius && this.wind > 0) || (obj.x == this.radius && this.wind < 0))))
        obj.dx += this.wind;
      if(!(obj.y == this.canvas.height - this.radius && obj.dy == 0))
        obj.dy += this.gravity;
      if(this.collissionsActive){
        for(let x = 0; x < this.objects.length; x++){
          if(!this.objects[x] || x == i) continue;
          let dist = Math.sqrt(Math.pow(obj.x - this.objects[x].x,2) + Math.pow(obj.y - this.objects[x].y,2))
          if(dist < 2 * this.radius){
            let theta = Math.asin((obj.y - this.objects[x].y) / dist);
            theta = Math.abs(theta);
            let v1 = Math.sqrt(Math.pow(obj.dx,2) + Math.pow(obj.dy,2));
            let v2 = Math.sqrt(Math.pow(this.objects[x].dx,2) + Math.pow(this.objects[x].dy,2));
            let v1_n = v1 * Math.sin(theta), v1_t = v1 * Math.cos(theta);
            let v2_n = v2 * Math.sin(theta), v2_t = v2 * Math.cos(theta);

            obj.dx = (obj.dx > 0 ? v1_t : -v1_t) * Math.sin(theta) + (obj.x > this.objects[x].x ? v2_n : -v2_n) * Math.cos(theta);
            obj.dy = (obj.dy > 0 ? v1_t : -v1_t) * Math.cos(theta) + (obj.y > this.objects[x].y ? v2_n : -v2_n) * Math.sin(theta);
            this.objects[x].dx = (this.objects[x].dx > 0 ? v2_t : -v2_t) * Math.sin(theta) + (obj.x < this.objects[x].x ? v1_n : -v1_n) * Math.cos(theta);
            this.objects[x].dy = (this.objects[x].dy > 0 ? v2_t : -v2_t) * Math.cos(theta) + (obj.y < this.objects[x].y ? v1_n : -v1_n) * Math.sin(theta);
            return;
          }
        }
      }
      //Right wall or Left wall
      if(!this.sideWalls && obj.x > this.canvas.width + this.radius)
        obj.x = -this.radius;
      else if(!this.sideWalls && obj.x < -this.radius)
        obj.x = this.canvas.width + this.radius;
      if(this.sideWalls && ((obj.x > this.canvas.width - this.radius && obj.dx > 0) || (obj.x < this.radius && obj.dx < 0))){
        obj.dx = (-obj.dx) * Math.sqrt(this.bouncePercent);
        if(Math.abs(obj.dx) < velocityRound * Math.abs(this.wind)){
          obj.x = obj.x > this.canvas.width-this.radius ? this.canvas.width-this.radius : this.radius;
          obj.dx = 0;
        }
      }
      //Floor or Roof
      if((obj.y > this.canvas.height - this.radius && obj.dy > 0) || (obj.y < this.radius && obj.dy < 0)){
        obj.dy = (-obj.dy) * Math.sqrt(this.bouncePercent) *(1 - (Math.random()*this.bounceRandomness*.01 - (this.bounceRandomness*.01/2)));
        obj.dx += Math.random()*this.bounceRandomnessX*obj.dy*.01 - (this.bounceRandomnessX*obj.dy*.01/2);
        if(Math.abs(obj.dy) < velocityRound * this.gravity && obj.y > this.canvas.height - this.radius){
          obj.y = this.canvas.height - this.radius;
          obj.dy = 0;
        }
      }
    },
    drawShape(i){
      var obj = this.objects[i];
      let ctx = this.ctx;
      ctx.beginPath();
      ctx.arc(obj.x, obj.y, this.radius, 0, Math.PI*2);
      ctx.fillStyle = this.colorType == 'random' ? obj.color : this.color;
      ctx.fill();
      ctx.closePath();
      this.updateObjPosition(i);
      
    },
    drawFrame(){
      if(!this.keepColorTrails)
        this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
      this.updateColor();
      for(let i in this.objects){
        if(!this.objects[i].hide)
          this.drawShape(i);
      }
      if(!this.frameDelay)
        requestAnimationFrame(this.drawFrame);
      else
        setTimeout(()=>requestAnimationFrame(this.drawFrame), this.frameDelay);
    },
    updateColor(){
      if(this.colorType != 'change' && this.colorType != 'change-gray')
        return;
      if(this.colorFrom == null || this.colorTo == null || (this.colorFrom.r < this.colorTo.r && this.colorCurrent.r > this.colorTo.r) || (this.colorFrom.r > this.colorTo.r && this.colorCurrent.r < this.colorTo.r) || (this.colorFrom.r == this.colorTo.r)){
        //pick new colors
        if(this.colorType == 'change-gray'){
          if(this.colorTo != null)
            this.colorFrom = {...this.colorTo};
          else 
            this.colorFrom = {r:0, g:0, b:0}
          this.colorTo = this.colorFrom.r > 127 ? {r:0, g:0, b:0} : {r:245, g:245, b:245};
        }
        else{
          if(this.colorTo != null)
            this.colorFrom = {...this.colorTo};
          else
            this.colorFrom = {
              r:Math.floor(Math.random()*255), 
              g:Math.floor(Math.random()*255), 
              b:Math.floor(Math.random()*255) 
            };
          this.colorTo = {
            r:Math.floor(Math.random()*255), 
            g:Math.floor(Math.random()*255), 
            b:Math.floor(Math.random()*255) 
          };
        }
      }
      if(!this.colorCurrent)
        this.colorCurrent = {...this.colorFrom};
      else{
        this.colorCurrent.r += (this.colorTo.r - this.colorFrom.r) * this.colorTransitionSpeed / 2000;
        this.colorCurrent.g += (this.colorTo.g - this.colorFrom.g) * this.colorTransitionSpeed / 2000;
        this.colorCurrent.b += (this.colorTo.b - this.colorFrom.b) * this.colorTransitionSpeed / 2000;
      }
      this.color = 'rgb('+Math.floor(this.colorCurrent.r)+', '+Math.floor(this.colorCurrent.g)+', '+Math.floor(this.colorCurrent.b)+')'
    },
    changeAllVx(deltaV){
      this.objects.forEach(element => {
        element.dx += deltaV;
      });
    },
    changeAllVy(deltaV){
      this.objects.forEach(element => {
        element.dy -= deltaV;
      });
    },
    getAverageBallSpeed() {
      let sum = 0;
      this.objects.forEach((element) => {
        sum += Math.sqrt(Math.pow(element.dx, 2) + Math.pow(element.dy, 2));
      })
      return (sum / this.objects.length);
    },
    addBall(patternX){
      let ball = {
        x: !isNaN(patternX) ? patternX : (this.getRandX()),
        y: 0,
        dx: 0,
        dy: this.gravity == 0 || (this.xDrop=='center' && this.collissionsActive) ? 5 : 0,
        //radius: 50,
        color: (randomColors[Math.floor(Math.random()*4)])
      }
      this.objects.push(ball);
    },
    addManyBalls(n){
      const margin = 5;
      let patternX = this.radius+margin;
      for(let i = 0; i < n; i++){
        if(this.xDrop == 'pattern'){
          let x = patternX;
          setTimeout(() => this.addBall(x), i*this.dropDelay);
          patternX += 2*this.radius+margin;
          if(patternX > this.canvas.width-this.radius-margin)
            patternX = this.radius;
        }
        else
          setTimeout(() => this.addBall(), i*this.dropDelay);
      }
    },
    getRandX(){
      if(this.xDrop == 'random')
        return Math.random()*(this.canvas.width - 2*this.radius) + this.radius;
      return this.canvas.width/2;
    },
    reset(){
      const margin = 5;
      let patternX = this.radius+margin;
      for(let i in this.objects){
        let obj = this.objects[i];
        obj.hide =  true;
        let x = patternX;
        setTimeout(() => {
          obj.x = this.xDrop == 'pattern' ? x : this.getRandX();
          obj.y = 0;
          obj.dy = 1;
          obj.dx = 0;
          obj.hide = false;
        }, i*this.dropDelay+300)
        patternX += 2*this.radius+margin;
        if(patternX > this.canvas.width-this.radius-margin)
          patternX = this.radius;
      }
    },
    clear(){
      this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
      this.objects.length = 0;
    },
    resetAll(){
      this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
      this.objects.length = 0;
      this.gravity = 0.2;
      this.radius = 15;
      this.bouncePercent= 0.9;
      this.wind = 0.0;
      this.color = '#0095DD';
      this.dropDelay = 20;
      this.keepColorTrails = false;
      this.collissionsActive = false;
      this.sideWalls = true;
      this.colorTransitionSpeed = 10;
      this.colorType = 'change';
      this.bounceRandomness = 5;
      this.bounceRandomnessX = 30;
      this.xDrop = 'center';
      this.frameDelay = 0;
    },

    colorSettingsClick(){
      this.colorSettingsClosed = !this.colorSettingsClosed;
      if(this.colorSettingsClosed){
        document.getElementById('color-settings').style.transition = 'height .3s ease .1s, opacity .3s'
        setTimeout(() => document.getElementById('color-settings').style.overflowY = this.colorSettingsClosed ? 'hidden' : '', 1000)
      }
      else{
        document.getElementById('color-settings').style.transition = '';
        document.getElementById('color-settings').style.overflowY = '';
      }
    },
    physicsSettingsClick(){
      this.physicsSettingsClosed = !this.physicsSettingsClosed;
      if(this.physicsSettingsClosed){
        document.getElementById('physics-settings').style.transition = 'height .3s ease .1s, opacity .3s'
        setTimeout(() => document.getElementById('physics-settings').style.overflowY = this.physicsSettingsClosed ? 'hidden' : '', 1000)
      }
      else{
        document.getElementById('physics-settings').style.transition = '';
        document.getElementById('physics-settings').style.overflowY = '';
      }
    },
    mainSettingsClick(){
      this.mainSettingsClosed = !this.mainSettingsClosed;
      if(this.mainSettingsClosed){
        document.getElementById('main-settings').style.transition = 'height .3s ease .1s, opacity .3s'
        setTimeout(() => document.getElementById('main-settings').style.overflowY = this.mainSettingsClosed ? 'hidden' : '', 0)
      }
      else{
        document.getElementById('main-settings').style.transition = 'all .7s';
        document.getElementById('main-settings').style.overflowY = '';
      }
    }
  }
}
</script>

<style scoped>
#game-canvas{
  margin: 0 auto 20px;
}
.game-screen{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap-reverse;
  align-items: flex-end;
}
.options{
  top: 60px;
  display: flex;
  flex-direction: column;
  margin-left: 30px;
  margin-right: 20px;
  padding-bottom: 30px;
  padding-right: 20px;
  overflow-y: scroll;
  height: 790px;
}
.check-row{
  text-align: left;
}
.check-col{
  text-align: left;
  display: flex;
  flex-direction: column;
}
.check-row label{
  margin-right: 20px;
}
.expand-settings input{
  margin-bottom: 10px;
}
.expand-settings label{
  text-align: left;
}
.expand-settings button{
  padding: 5px;
  margin: 2px;
}
.expand-settings{
  display: flex;
  flex-direction: column;
  background: lightgray;
  transition: height 1s, opacity .5s ease-in .3s;
}
.expand-settings-button{
  cursor: pointer;
  padding: 10px;
  margin: 10px 0;
  z-index: 999;
  border-radius: 6px;
  border: none;
}
#main-settings-button{
  margin-top: 20px;
}
</style>
